import PowerController from "../Controller/PowerController";
import Enemy from "./Enemy";

import { _decorator, Component,CircleCollider2D, Node, Prefab,Animation,Camera,Label, instantiate, RigidBody2D, Vec2, Vec3, toRadian, math, Skeleton, sp, tween, EventTouch } from 'cc';

const { ccclass, property } = _decorator;

@ccclass
export default class NewClass extends Component {

    @property(PowerController)
    PowerController: PowerController = null;

    protected start(): void {

    }

    protected onCollisionEnter(other: any, self: any) {
        if (other.tag == 1) {
            if(this.PowerController.isTransforming && this.PowerController.isLevel2) {
                other.node.getComponent(Enemy).die();
            }
        }

        if (other.tag == 3) {
            if(this.PowerController.isTransforming && this.PowerController.isLevel3) {
                other.node.getComponent(Enemy).die();
            }
        }
    }
}
